import React, {Component} from 'react'

import Button  from "./Button.jsx";
import Nuevo from "./Nuevo.jsx";
import Buscar from "./Buscar.jsx";
import Explorar from './Explorar.jsx';


export default class Body extends Component{
    constructor(props){
        super(props);
        this.state = {
          pages: 'Nuevo',
          buttonNew: "selected",
          buttonSearch: "noselected",
          buttonBrowse: "noselected",
          newRepairment: null
        }
      };
    
    handleClickMenu(button){
      
      switch (button) {
        case 'Nuevo': 
        this.setState({pages: button,
                       buttonNew: "selected",
                       buttonSearch: "noselected",
                       buttonBrowse: "noselected"});
           break;
        case 'Buscar':
          this.setState({pages: button,
                         buttonNew: "noselected",
                         buttonSearch: "selected",
                         buttonBrowse: "noselected"});
          break;
        case 'Explorar':
          this.setState({pages: button,
                         buttonNew: "noselected",
                         buttonSearch: "noselected",
                         buttonBrowse: "selected"});
          break;
        default:
            break;
      }
    }
    
    
    renderMenu(){
        return(
            
                <ul className="menuHeader">
                    <Button name='Nuevo' onClick={()=>this.handleClickMenu('Nuevo')} className={this.state.buttonNew} />
                    <Button name='Buscar' onClick={()=>this.handleClickMenu('Buscar')} className={this.state.buttonSearch}  />
                    <Button name='Explorar' onClick={()=>this.handleClickMenu('Explorar')} className={this.state.buttonBrowse} />
                </ul>
            
        )
    }
    renderHeader(){
        return(
                <div className="Header"><img id="logo" alt="logo" src={this.props.logo} />

            {this.renderMenu()}</div>
        )
    }
    renderBody(){
        switch (this.state.pages) {
            case 'Nuevo': 
               return(<Nuevo onSubmit={()=>this.handleClickMenu('Explorar')}/>);
               break;
            case 'Buscar':
                return(<Buscar search={true} />);
              break;
            case 'Explorar':
                return(<Explorar url={'/api/repairments'} />);
              break;
            default:
                break;
          }
    }

    render(){
        
    return(    
            <div className='bodyApp'>
               <div className="Header">
                   {this.renderHeader()}

               </div>
               <div className="bodyApp">
                   {this.renderBody()}
               </div>

            </div>        
    )
    }
}