import React, {Component} from 'react'
import Button  from "./Button.jsx";


export default class Repairment extends Component{
    constructor(props){
        super(props);
        this.state ={
            "customer_name": this.props.repairment["customer_name"],
            "customer_phone": this.props.repairment["customer_phone"],
            "type": this.props.repairment["type"],
            "manufacturer": this.props.repairment["manufacturer"],
            "model": this.props.repairment["model"],
            "serial_number": this.props.repairment["serial_number"],
            "backup": this.props.repairment["backup"],
            "password": this.props.repairment["password"],
            "reception_date": this.props.repairment["reception_date"],
            "withdrawal_date":"",
            "estimated_cost": this.props.repairment["estimated_cost"],
            "description": this.props.repairment["description"],
            "id":this.props.repairment.id,
            "estimated_cost_parsed": this.props.repairment["estimated_cost"]/100,
            disabled: 'disabled',
            hide: 'formBtnHidden'
        }
        
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      };
      handleChange(event){
        const target = event.target;
        const value = target.value;
        const name = target.name;
        
        if(name==='estimated_cost_parsed'){
            var matched=value.match(/[0-9]+/g)
            this.setState({
                'estimated_cost': matched.join(''),
                'estimated_cost_parsed': value
            })
        }
        console.log(value)
        this.setState({
            [name] : value
        });
    }
    
    handleSubmit(event){
        
        event.preventDefault();
        
        let putUrl= 'http://localhost:5000/api/repairments/' + this.props.repairment.id
        
        fetch(putUrl,{  method: "PUT",
                        headers: {'Content-Type': 'application/json'},
                        body: JSON.stringify(this.state)
                        })
            .then(()=>{this.setState({disabled: 'disabled', hide:'formBtnHidden'});alert('Reparación modificada'); console.log(this.state)})
            .catch(error=>console.error('Error:', error))
        
    }

    handleClick(action){
        let pdfUrl='http://localhost:5000/files/repairment-'+ this.props.repairment.id +'.pdf'
        let deleteUrl='http://localhost:5000/api/repairments/'+ this.props.repairment.id
        switch (action) {
            case 'back':
                this.props.onClickBack();
                break;
            case 'pdf':
                window.open(pdfUrl, "_blank");
                break;
            case 'delete':
                if(window.confirm('Vas a borrar la reparación')){
                fetch(deleteUrl,{method: "DELETE"})
                    .then(this.props.onClickBack)}
                break;
            case 'edit':
                this.setState({disabled: '',hide:'formBtn'})
                break;
            default:
                break;
        }

    }
    render(){
        
        return (
            <div id="formEdit">
           
           <form onSubmit={this.handleSubmit} className="form">
           
           <h1>Reparación Id: {this.state.id}</h1>
           <div className="buttons">
                <Button name='Volver' onClick={()=>this.handleClick('back')} className="btn" id="back"/>
                <Button name='Imprimir' onClick={()=>this.handleClick('pdf')} className="btn" id="pdf"/>
                <Button name='Borrar' onClick={()=>this.handleClick('delete')} className="btn" id="delete"/>
                <Button name='Editar' onClick={()=>this.handleClick('edit')} className="btn" id="edit" />
                </div>
           <h3 id="client">Datos del cliente</h3>
            <div className="name"> 
               <label for="nombre">Nombre</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="customer_name"   
                      value={this.state.customer_name}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required />
               </div>
               <div className="phone">
               <label for="telefono Movil">Teléfono móvil</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="customer_phone"   
                      value={this.state.customer_phone}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
              
               
               <h3 className="equipment">Datos del equipo</h3>
               <div className="type">
               <label for="Equipo">Equipo</label>
               <input type="text" 
                      
                      className="inputText" 
                      name="type"   
                      value={this.state.type}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
                <div className="manufacturer">
               <label for="Fabricante">Fabricante</label>
               <input type="text" 
                      
                      className="inputText" 
                      name="manufacturer"   
                      value={this.state.manufacturer}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
                <div className="model">
               <label for="Modelo">Modelo</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="model"   
                      value={this.state.model}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
                <div className="serial_number">
               <label for="Numero de serie">Número de serie</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="serial_number"   
                      value={this.state.serial_number}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
                <div className="backup">
               <label for="Copia de seguridad">Copia de seguridad</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="backup"   
                      value={this.state.backup}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
                <div className="password">
               <label for="Contraseña">Contraseña</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="password"   
                      value={this.state.password}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
                <div className="reception_date">
               <label for="Recepcion">Fecha de recepción</label>
               <input type="date" 
                       
                      className="inputText" 
                      name="reception_date"   
                      value={this.state.reception_date}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
                <div className="estimated_cost">
               <label for="Presupuesto">Costo aproximado de reparación</label>
               <input type="text" 
                      className="inputText" 
                      name="estimated_cost_parsed"   
                      value={this.state.estimated_cost_parsed}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/>
                </div>
                <div className="withdrawal_date">
               <label for="retirada">Fecha de retirada</label>
               <input type="date" 
                      className="inputText" 
                      name="withdrawal_date"   
                      value={this.state["withdrawal_date"]}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      />
                </div>
                <div className="description">
               <label for="Averia">Problema del equipo</label>
               <textarea type="text" 
                       
                      
                      name="description"   
                      value={this.state.description}                   
                      onChange={this.handleChange} 
                      disabled={this.state.disabled}
                      required/></div>
               
                   <input type="submit" value="Guardar Reparación" className={this.state.hide}></input>
           </form>
                
            </div>
        )
    }
}