import React, {Component} from 'react'
import Button  from "./Button.jsx";
import Repairment  from "./Repairment.jsx";


export default class Explorar extends Component{
    constructor(props){
        super(props);
        this.state ={
            getRepairList:[],
            isLoaded: false,
            mode: 'browse' ,
            repairmentIndex: 0           
        };            
      }

    componentDidMount(){
        fetch(this.props.url)
            .then( response=>response.json())
            .then(data=>{this.setState(
                                {getRepairList:Array.from(data),
                                isLoaded: true})})
                                
    }
    componentDidUpdate(){
        if(!this.state.isLoaded){
        fetch(this.props.url)
            .then( response=>response.json())
            .then(data=>{this.setState(
                                {getRepairList:Array.from(data),
                                isLoaded: true})})
                                }
    }

    handleClick(action, index){        
        if(action=='view'){
        this.setState({
            mode: action,
            repairmentIndex: index
        })}
        else{
            this.setState({
                mode: action,
                isLoaded: false
            })   
        }
    }

    render(){
        console.log(this.state)
        if(!this.state.isLoaded){
            return(<div>cargando reparaciones</div>)
        }
        else{
            if(this.state.mode==='view'){                
                return(<Repairment repairment={this.state.getRepairList[this.state.repairmentIndex]} onClickBack={()=>this.handleClick('browse',0)}/>)
            }
            else if(this.state.mode==='browse'){
                    return(<div> 
                        <h1>Explorar reparaciones</h1>       
                        <div><table id="tbl-repairments" className="table">
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th>N/S</th>
                                        <th>Móvil</th>
                                        <th>Nombre</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.state.getRepairList.map((repairment,index)=> (<tr key={repairment.id}>
                                                                                    <th>{repairment.id}</th>
                                                                                    <th>{repairment['serial_number']}</th>
                                                                                    <th>{repairment['customer_phone']}</th>
                                                                                    <th>{repairment['customer_name']}</th>
                                                                                    <th>
                                                                                        <Button name='Ver' onClick={()=>this.handleClick('view',index)} className="btn" id="view" />
                                                                                    </th>
                                                                                       </tr>))}
                                </tbody>
                            </table>
                        </div>
                    </div>)
            }
        }
    }
}