import React, {Component} from 'react'


export default class Nuevo extends Component{
    constructor(props){
        super(props);
        this.state ={
            "customer_name": "",
            "customer_phone": "",
            "type": "",
            "manufacturer": "",
            "model": "",
            "serial_number": "",
            "backup": "",
            "password": "",
            "reception_date": "",
            "estimated_cost": "",
            "description": "",
            "estimated_cost_parsed":""
        }
        
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      };
    handleChange(event){
        const target = event.target;
        const value = target.value;
        const name = target.name;
        
        if(name==='estimated_cost_parsed'){
            var matched=value.match(/[0-9]+/g)
            this.setState({
                'estimated_cost': matched.join(''),
                'estimated_cost_parsed': value
            })
        }
        this.setState({
            [name] : value
        });
    }
    handleSubmit(event){
        console.log(this.state);
        event.preventDefault();
        fetch('/api/repairments',{method: "POST",
                   headers: {
                            'Content-Type': 'application/json'},
                             body: JSON.stringify(this.state)})

            .then(response => {this.props.onSubmit();console.log(response.ok);})
            .catch(error=>console.error('Error:', error))
        this.setState({
            "customer_name": "",
            "customer_phone": "",
            "type": "",
            "manufacturer": "",
            "model": "",
            "serial_number": "",
            "backup": "",
            "password": "",
            "reception_date": "",
            "estimated_cost": "",
            "description": "",
            "estimated_cost_parsed":""
        })
            
    }

    
    render(){
    return(
       <div id="formNew">
           
           <form onSubmit={this.handleSubmit} className="form">
           
           <h1>Nueva reparación</h1>
           
           <h3 id="client">Datos del cliente</h3>
            <div  className="name"> 
               <label for="nombre">Nombre</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="customer_name"   
                      value={this.state.customer_name}                   
                      onChange={this.handleChange} 
                      required />
               </div>
               <div  className="phone">
               <label for="telefono Movil">Teléfono móvil</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="customer_phone"   
                      value={this.state.customer_phone}                   
                      onChange={this.handleChange} 
                      required/></div>
              
               
               <h3 className="equipment">Datos del equipo</h3>
               <div className="type">
               <label for="Equipo">Equipo</label>
               <input type="text" 
                      
                      className="inputText" 
                      name="type"   
                      value={this.state.type}                   
                      onChange={this.handleChange} 
                      required/></div>
                <div className="manufacturer" >
               <label for="Fabricante">Fabricante</label>
               <input type="text" 
                      
                      className="inputText" 
                      name="manufacturer"   
                      value={this.state.manufacturer}                   
                      onChange={this.handleChange} 
                      required/></div>
                <div className="model">
               <label for="Modelo">Modelo</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="model"   
                      value={this.state.model}                   
                      onChange={this.handleChange} 
                      required/></div>
                <div className="serial_number" >
               <label for="Numero de serie">Número de serie</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="serial_number"   
                      value={this.state.serial_number}                   
                      onChange={this.handleChange} 
                      required/></div>
                <div className="backup">
               <label for="Copia de seguridad">Copia de seguridad</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="backup"   
                      value={this.state.backup}                   
                      onChange={this.handleChange} 
                      required/></div>
                <div className="password">
               <label for="Contraseña">Contraseña</label>
               <input type="text" 
                       
                      className="inputText" 
                      name="password"   
                      value={this.state.password}                   
                      onChange={this.handleChange} 
                      required/></div>
                <div className="reception_date">
               <label for="Recepcion">Fecha de recepción</label>
               <input type="date" 
                       
                      className="inputText" 
                      name="reception_date"   
                      value={this.state.reception_date}                   
                      onChange={this.handleChange} 
                      required/></div>
                <div className="estimated_cost">
               <label for="Presupuesto">Costo aproximado de reparación (000.00)</label>
               <input type="text" 
                      
                      className="inputText" 
                      name="estimated_cost_parsed"   
                      value={this.state.estimated_cost_parsed}                   
                      onChange={this.handleChange} 
                      required/>
                </div>
                <div className="description">
               <label for="Averia">Problema del equipo</label>
               <textarea type="text" 
                       
                      
                      name="description"   
                      value={this.state.description}                   
                      onChange={this.handleChange} 
                      required/></div>
               
                   <input type="submit" value="Guardar Reparación" className="formBtn" ></input>
           </form>

       </div>
    )
}
}