import React, {Component} from 'react'
import Explorar from './Explorar.jsx'


export default class Buscar extends Component{
    constructor(props){
        super(props);
        this.state ={
            search: this.props.search,
            url:'/api/repairments',
            "customer_name": "",
            "customer_phone": "",
            "serial_number": "",
            "id": ""
        }
        
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      };

    handleChange(event){
        const target = event.target;
        const value = target.value;
        const name = target.name;

        this.setState({
            [name] : value
        });
    }
    handleSubmit(event){
        
        event.preventDefault();
        if(this.state['customer_name']===""){
            if(this.state['customer_phone']===""){
                if(this.state['serial_number']===""){
                    if(this.state['id']!==""){
                        this.setState({
                            search:!this.state.search,
                            url: '/api/repairments?id=' + this.state['id']
                            })
                        console.log('hola: '+ this.state.url)
                    }else{
                        alert('Introduce al menos un campo de búsqueda')
                    }
                }else{
                    this.setState({
                        search:!this.state.search,
                        url: '/api/repairments?seral_number=' + this.state['serial_number']
                        })
                }
            }else{
                this.setState({
                    search:!this.state.search,
                    url: '/api/repairments?customer_phone=' + this.state['customer_phone']
                    })
            }
        }else{
            this.setState({
                search:!this.state.search,
                url: '/api/repairments?customer_name=' + this.state['customer_name']
                })
        }
            
        
        
            
    }
    render(){
        console.log(this.props.search)
        if(this.state.search&&this.props.search){
            return (
            
                <div id="formNew">
            <form onSubmit={this.handleSubmit} className="form">
           
            <h1>Búsqueda</h1>
            <h3 id="client">Datos del cliente</h3>
            <div className="id"> 
                <label for="id">Id</label>
                <input type="text" 
                        
                       className="inputText" 
                       name="id"   
                       value={this.state.id}                   
                       onChange={this.handleChange} 
                       />
                
                </div>
             <div className="name"> 
                <label for="nombre">Nombre</label>
                <input type="text" 
                        
                       className="inputText" 
                       name="customer_name"   
                       value={this.state.customer_name}                   
                       onChange={this.handleChange} 
                       />
                
                </div>
                <div className="phone">
                <label for="telefono Movil">Teléfono móvil</label>
                <input type="text" 
                        
                       className="inputText" 
                       name="customer_phone"   
                       value={this.state.customer_phone}                   
                       onChange={this.handleChange} 
                       />
                
                </div>
                <div className="serial_number">
                <label for="Numero de serie">Número de serie</label>
                <input type="text" 
                        
                       className="inputText" 
                       name="serial_number"   
                       value={this.state.serial_number}                   
                       onChange={this.handleChange} 
                       />
                      
                </div>
                <input type="submit" value="Buscar" className="formBtn" ></input> 
                
            </form></div>
            )
        }else{
            return (<Explorar url={this.state.url}/>)
        }
    
}
}